@import url(https://unpkg.com/leaflet@1.5.1/dist/leaflet.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img.huechange-140 { -webkit-filter: hue-rotate(140deg); filter: hue-rotate(140deg); }
img.huechange-280 { -webkit-filter: hue-rotate(280deg); filter: hue-rotate(280deg); }
img.huechange-190 { -webkit-filter: hue-rotate(190deg); filter: hue-rotate(190deg); }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.full-size {
  height: 100%;
  width: 100%;
}

.split,
.gutter.gutter-horizontal,
.gutter.gutter-vertical {
  float: left;
}

.gutter.gutter-horizontal {
  cursor: ew-resize;
  height: 100%;
  background: 
    url('https://api.iconify.design/ic-round-drag-handle.svg?color=rgb(140%2C140%2C140)&height=2em&rotate=90deg') no-repeat center center,
    linear-gradient(to right, rgb(245,245,245), rgb(255,255,255));
}

.gutter:hover.gutter-horizontal {
  background: 
    url('https://api.iconify.design/ic-round-drag-handle.svg?color=rgb(140%2C140%2C140)&height=2em&rotate=90deg') no-repeat center center,
    linear-gradient(to right, rgb(235,235,235), rgb(245,245,245));
}

.gutter.gutter-vertical {
  cursor: eh-resize;
  width: 100%;
  background:
    url('https://api.iconify.design/ic-round-drag-handle.svg?color=rgb(140%2C140%2C140)&height=2em') no-repeat center center,
    linear-gradient(to bottom, rgb(245,245,245), rgb(255,255,255));
}

.gutter.gutter-vertical:hover {
  background: 
    url('https://api.iconify.design/ic-round-drag-handle.svg?color=rgb(140%2C140%2C140)&height=2em') no-repeat center center,
    linear-gradient(to right, rgb(235,235,235), rgb(245,245,245));
}
.leaflet-container {
    width: 100%;
    height: 100%;
}
.product-card {
    width: 15em!important;
    float: left
}

.product-img {
    height: 10em;
    object-fit: contain;
}
